.no-bottom-spacing{
  padding-bottom: 0px;
  div.mdl-cell{
    margin-bottom: 0px;
  }
}
slider p, slider slider-knob{
  text-align: center;
}
.slider-label{
  margin-bottom::0px;
}
.slider-overlay{
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  bottom:0;
  right:0;
  z-index: 5;
}
.slider{
  z-index: 6;
}
.slider-overlay.open{
  display: initial;
}
.slider-knob{
  width:36px;
  height:36px;
  border-radius:36px;
  color:#fff;
  text-align:center;
  border:3px solid #fff;
  margin: 0 auto;
}
.noUi-target{
  position: absolute;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  border: 0px;


}
.slider-num{
  width:50px;
  position: absolute;
  display: none;
  z-index: 6;
}
.noUi-base{
  background: #fff;
  height: 100px;
  border-radius: 4px;

}
.noUi-handle{
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14), 0 3px 1px -2px rgba(0,0,0,.2), 0 1px 5px 0 rgba(0,0,0,.12);
  background: #2196f3;
  border:0px;
}
.noUi-handle:before{
  background: transparent;
}

.green-handle .noUi-handle{
  background: #00e676;
}
.no-bottom-margin{
  margin-bottom: 0px;
}
.nav-active{
  background-color: rgba(0,0,0,0.1);
}
.card-title{
  margin-top: 0px;
}
.mdl-layout__header-row .mdl-navigation__link{
  padding: 0 16px;
}
